.ng2-c-pincode__wrapper {
  margin: 0 auto;
  max-width: 220px; }

.ng2-c-pincode__inner {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  text-align: center; }

.ng2-c-pincode__loader-container {
  position: absolute;
  width: 100%; }
  .ng2-c-pincode__loader-container .ng2-c-loader {
    border-color: #6ec60f;
    border-right-color: transparent; }

.ng2-c-pincode--digits .ng2-c-pincode__inner {
  height: 58px; }

.ng2-c-pincode--digits .ng2-c-pincode__item {
  appearance: none;
  background: #fcfcfc;
  border: 2px solid #e9e7e7;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box;
  color: #9b9b9b;
  font-size: 18px;
  height: 58px;
  margin: 0 0 10px;
  padding: 0 15px;
  width: 100%;
  font-size: 28px;
  margin-bottom: 0;
  padding: 0;
  text-align: center;
  width: 42px; }
  .ng2-c-pincode--digits .ng2-c-pincode__item.ng2-c-pincode__item--active {
    border-color: #28648a;
    color: #28648a;
    outline: 0;
    -webkit-text-fill-color: #28648a; }

.ng2-c-pincode--digits .ng2-c-pincode--error .ng2-c-pincode__item {
  border-color: #d14700;
  color: #d14700;
  -webkit-text-fill-color: #d14700; }

.ng2-c-pincode--bullets .ng2-c-pincode__inner {
  height: 45px; }

.ng2-c-pincode--bullets .ng2-c-pincode__items {
  width: 152px; }

.ng2-c-pincode--bullets .ng2-c-pincode__item {
  border: 1px solid #ffffff;
  border-radius: 12px;
  height: 12px;
  overflow: hidden;
  padding: 0;
  text-indent: -999px;
  vertical-align: middle;
  width: 12px; }
  .ng2-c-pincode--bullets .ng2-c-pincode__item.ng2-c-pincode__item--active {
    background: #ffffff; }

.ng2-c-pincode--bullets .ng2-c-pincode--error {
  animation: shake 0.75s cubic-bezier(0.5, 0.05, 0.2, 1); }

.ng2-c-pincode__items {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: space-around;
  margin: 0 auto;
  width: 100%; }

.ng2-c-pincode__item {
  background: none;
  opacity: 1; }

.ng2-c-pincode--alt .ng2-c-pincode__item {
  border-color: #28648a; }
  .ng2-c-pincode--alt .ng2-c-pincode__item.ng2-c-pincode__item--active {
    background: #28648a; }

@keyframes shake {
  10%,
  90% {
    transform: translateX(-1px); }
  20%,
  80% {
    transform: translateX(2px); }
  30%,
  50%,
  70% {
    transform: translateX(-3px); }
  40%,
  60% {
    transform: translateX(3px); } }
